// CustomNode.js
import React from 'react';
import { Handle, Position } from '@xyflow/react';

const CustomNode = ({ id, data, selected }) => {
  const [idLine, ...textLines] = data.label.split('\n');
  const mainText = textLines.join('\n');
  const media = data.media || [];

  const firstPhoto = media.find((m) => m.type === 'photo');

  const sourceHandleStyle = {
    width: selected ? 16 : 12,   // Увеличиваем видимый размер
    height: selected ? 16 : 12,
    background: '#3b82f6',
    border: '3px solid white',
    borderRadius: '50%',
    boxShadow: '0 0 5px 2px rgba(59, 130, 246, 0.7)', // Добавляем свечение для заметности
    transition: 'all 0.2s',
  };

  return (
    <div
      style={{
        backgroundColor: data.bgColor || '#ffffff',
        color: 'black',
        padding: 10,
        borderRadius: 10,
        minWidth: 160,
        textAlign: 'center',
        boxShadow: '0 4px 12px rgba(0, 0, 0, 0.3)',
        border: selected ? '2px solid #3b82f6' : 'none',
        transition: 'border 0.2s',
      }}
    >
      {/* Заголовок */}
      <div
        style={{
          backgroundColor: 'rgba(31, 41, 55, 0.7)',
          color: 'white',
          padding: '4px 8px',
          borderRadius: 6,
          marginBottom: 8,
          fontWeight: 'bold',
        }}
      >
        {idLine}
      </div>

      {/* Основной текст */}
      <div style={{ whiteSpace: 'pre-line', marginBottom: firstPhoto ? 8 : 0 }}>
        {mainText.split('\n').map((line, i) => (
          <div
            key={i}
            style={
              line.startsWith('📎')
                ? { color: '#6b7280', fontStyle: 'italic' }
                : {}
            }
          >
            {line}
          </div>
        ))}
      </div>

      {/* Фото превью */}
      {firstPhoto && (
        <img
          src={firstPhoto.url}
          alt="preview"
          style={{
            width: '100%',
            maxHeight: 100,
            objectFit: 'cover',
            borderRadius: 6,
          }}
        />
      )}

      <Handle type="target" position={Position.Top} style={{ visibility: 'hidden' }} />

      {/* Контейнер для увеличения области касания */}
      <div
        style={{
          position: 'absolute',
          bottom: '-20px', // Смещаем контейнер вниз
          left: '50%',
          transform: 'translateX(-50%)',
          width: '40px',   // Задаем большую ширину
          height: '40px',  // и высоту
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          cursor: 'pointer',
          // background: 'rgba(255, 0, 0, 0.2)', // Раскомментируйте для отладки области
        }}
      >
        <Handle
          type="source"
          position={Position.Bottom}
          style={sourceHandleStyle}
          // isConnectable={true} // Убедитесь, что соединение возможно
        />
      </div>
    </div>
  );
};

export default CustomNode;