// App.js

// --- Импорт необходимых библиотек и компонентов ---
import React, { useCallback, useEffect, useState, useRef, useMemo } from 'react';
import {
  ReactFlow,
  Background,
  Controls,
  MiniMap,
  useNodesState,
  useEdgesState,
  useReactFlow,
  ReactFlowProvider, // Провайдер для доступа к React Flow API в дочерних компонентах
} from '@xyflow/react';
import '@xyflow/react/dist/style.css'; // Стили для React Flow
import dagre from 'dagre'; // Библиотека для автоматического расположения узлов

// --- Импорт кастомных компонентов и API ---
import CustomEdge from './CustomEdge';
import CustomNode from './CustomNode';
import ContextMenu from './ContextMenu';
import EdgeContextMenu from './EdgeContextMenu';
import EffectsModal from './EffectsModal';
import {
  apiUpdateFragmentText,
  apiDeleteFragment,
  apiRenameFragment,
  apiConnectFragments,
  apiCreateAndConnect,
  apiUpdateChoice
} from './api';


// --- Логика расположения узлов (Dagre) ---

// Создаем экземпляр графа для Dagre
const dagreGraph = new dagre.graphlib.Graph();
dagreGraph.setDefaultEdgeLabel(() => ({})); // Настройка по умолчанию для ребер

// Константы для размеров узлов, используемые при расчете макета
const nodeWidth = 172;
const nodeHeight = 36;

/**
 * Рассчитывает позиции узлов и ребер с помощью Dagre.
 * @param {Array} nodes - Массив узлов.
 * @param {Array} edges - Массив ребер.
 * @param {string} direction - Направление макета ('TB' - сверху вниз, 'LR' - слева направо).
 * @returns {Object} - Объек с массивами узлов и ребер, имеющими вычисленные позиции.
 */
const getLayoutedElements = (nodes, edges, direction = 'TB') => {
  // Настройка параметров графа
  dagreGraph.setGraph({ rankdir: direction, nodesep: 100, ranksep: 150 });

  // Добавляем узлы и ребра в граф Dagre для расчета
  nodes.forEach((node) => dagreGraph.setNode(node.id, { width: nodeWidth, height: nodeHeight }));
  edges.forEach((edge) => dagreGraph.setEdge(edge.source, edge.target));

  // Выполняем расчет макета
  dagre.layout(dagreGraph);

  // Центрируем граф относительно главного узла 'main_1'
  const mainNodeId = 'main_1';
  const mainNodePos = dagreGraph.node(mainNodeId);
  const offsetX = mainNodePos?.x || 0;
  const offsetY = mainNodePos?.y || 0;

  nodes.forEach((node) => {
    const nodeWithPos = dagreGraph.node(node.id);
    // Устанавливаем точки подключения ребер
    node.targetPosition = 'top';
    node.sourcePosition = 'bottom';
    // Применяем вычисленные и отцентрированные координаты
    node.position = { x: nodeWithPos.x - offsetX, y: nodeWithPos.y - offsetY };
  });

  return { nodes, edges };
};


// --- Вспомогательные функции ---

/**
 * Определяет цвет узла в зависимости от его состояния.
 * @param {string} id - ID фрагмента.
 * @param {Object} fragment - Данные фрагмента.
 * @returns {string} - HEX-код цвета.
 */
const getNodeColor = (id, fragment) => {
    const hasText = !!fragment.text?.trim() && fragment.text !== '(пусто)';
    const hasMedia = Array.isArray(fragment.media) && fragment.media.length > 0;
    if (!hasText && !hasMedia) return '#fecaca'; // Красный (пустой)
    if (!fragment.choices || fragment.choices.length === 0) return '#fef3c7'; // Желтый (конечный)
    if (id === 'main_1') return '#86efac'; // Зеленый (главный)
    if (/_1$/.test(id) && id !== 'main_1') return '#bbf7d0'; // Светло-зеленый (сцена)
    return '#e0f2fe'; // Синий (обычный)
};

/**
 * Возвращает русское название типа медиафайла.
 * @param {string} type - Тип медиа ('photo', 'video', etc.).
 * @returns {string} - Русское название.
 */
const mediaLabel = (type) => ({
    photo: 'фото',
    video: 'видео',
    animation: 'анимации',
    audio: 'аудио'
}[type] || type);

// --- Основной компонент приложения ---

function App() {
  // --- Состояния компонента (State) ---
  const [nodes, setNodes, onNodesChange] = useNodesState([]);
  const [edges, setEdges, onEdgesChange] = useEdgesState([]);
  const [menu, setMenu] = useState(null); // Состояние для контекстного меню узла
  const [edgeMenu, setEdgeMenu] = useState(null); // Состояние для контекстного меню ребра
  const [effectsModal, setEffectsModal] = useState({ isOpen: false, edge: null }); // Состояние для модального окна эффектов
  const [showMiniMap, setShowMiniMap] = useState(true); // Состояние для отображения миникарты
  const wasConnectedRef = useRef(false);

  // --- Ссылки (Refs) и хуки React Flow ---
  const reactFlowWrapper = useRef(null); // Ссылка на DOM-элемент обертки React Flow
  const connectingNodeId = useRef(null); // Хранит ID узла, из которого начали тянуть связь
  const { screenToFlowPosition, getNodes } = useReactFlow(); // Хук для конвертации экранных координат в координаты Flow Достаем getNodes
  // --- Получение ID пользователя и истории из URL ---
  // useMemo кэширует результат, чтобы не вычислять его при каждом рендере
  const { userId, storyId } = useMemo(() => {
    const pathname = window.location.pathname;
    const [uid, sid] = pathname.slice(1).split('_');
    return { userId: uid, storyId: sid };
  }, []);

  const generateUniqueFragmentId = (baseName, existingIds) => {
    const regex = /^(.*?)(?:_(\d+))?$/;
    const match = baseName.match(regex);

    let nameBase = match[1]; // например, "идтивперёд"
    let suffix = match[2] ? parseInt(match[2], 10) : null;

    // Проверка на прямое совпадение без числа
    if (!suffix && !existingIds.has(`${nameBase}_1`)) {
      return `${nameBase}_1`;
    }

    // Всегда ищем следующую свободную форму: base_1, base_2, ...
    let i = 1;
    while (existingIds.has(`${nameBase}_${i}`)) {
      i++;
    }
    return `${nameBase}_${i}`;
  };
  // --- Функции обработки данных ---
  // --- НОВАЯ ФУНКЦИЯ: Сохранение позиций на сервер ---
  const saveCurrentNodePositions = useCallback(() => {
    // Не сохраняем, если нет ID истории или пользователя
    if (!userId || !storyId) return;

    const currentNodes = getNodes();
    if (!currentNodes.length) return;

    // Собираем объект с позициями { nodeId: { x, y } }
    const positionsToSave = currentNodes.reduce((acc, node) => {
      acc[node.id] = node.position;
      return acc;
    }, {});

    // Отправляем данные на сервер в фоновом режиме
    fetch(`/api/story/${userId}/${storyId}/positions`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(positionsToSave),
    })
    .then(res => {
        if(!res.ok) console.error("Ошибка при сохранении позиций");
    })
    .catch(error => console.error("Сетевая ошибка при сохранении позиций:", error));
  }, [userId, storyId, getNodes]);
  /**
   * Преобразует данные истории из API в формат узлов и ребер для React Flow.
   */
  // --- ОБНОВЛЕННАЯ ЛОГИКА: Обработка данных и применение позиций ---
  const processStoryData = useCallback((storyData, positionsData) => {
    if (!storyData?.fragments) return;

    const initialNodes = [];
    const initialEdges = [];

    Object.entries(storyData.fragments).forEach(([fragmentId, fragment]) => {
      const mediaCounts = (fragment.media || []).reduce((acc, { type }) => ({ ...acc, [type]: (acc[type] || 0) + 1 }), {});
      const mediaDescription = Object.entries(mediaCounts).map(([type, count]) => `${count} ${mediaLabel(type)}`).join(', ');
      const labelText = [fragmentId, fragment.text || '(пусто)', mediaDescription && `📎 ${mediaDescription}`].filter(Boolean).join('\n');
      const extendedMedia = (fragment.media || []).map(m => ({ ...m, url: `/api/tgfile/${m.file_id}` }));

      initialNodes.push({
        id: fragmentId,
        type: 'custom',
        position: { x: 0, y: 0 }, // Позиция по умолчанию
        data: {
          label: labelText,
          bgColor: getNodeColor(fragmentId, fragment),
          media: extendedMedia,
          fragment: fragment,
        },
      });

      if (fragment.choices) {
        fragment.choices.forEach((choice, i) => {
          const effectText = choice.effects?.length ? choice.effects.map(e => `${e.stat}: ${e.value}`).join(', ') : '';
          initialEdges.push({
            id: `${fragmentId}->${choice.target}_${i}`,
            source: fragmentId,
            target: choice.target,
            type: 'custom',
            animated: true,
            style: { stroke: '#2563eb' },
            data: {
              text: choice.text,
              effectsText: effectText,
              originalEffects: choice.effects || [],
              choiceIndex: i,
            },
          });
        });
      }
    });

    const hasPositions = positionsData && Object.keys(positionsData).length > 0;

    if (hasPositions) {
      // Если есть сохраненные позиции, применяем их
      initialNodes.forEach(node => {
        if (positionsData[node.id]) {
          node.position = positionsData[node.id];
        }
      });
      setNodes(initialNodes);
      setEdges(initialEdges);
    } else {
      // Иначе используем автоматическую раскладку
      const { nodes: layoutedNodes, edges: layoutedEdges } = getLayoutedElements(initialNodes, initialEdges);
      setNodes(layoutedNodes);
      setEdges(layoutedEdges);
    }
  }, [setNodes, setEdges]);


  // --- ОБНОВЛЕННАЯ ЛОГИКА: Загрузка данных истории и позиций ---
  useEffect(() => {
    const fetchData = async () => {
      if (!userId || !storyId) return;
      try {
        // Загружаем данные истории и позиции одновременно
        const [storyRes, positionsRes] = await Promise.all([
          fetch(`/api/story/${userId}/${storyId}`),
          fetch(`/api/story/${userId}/${storyId}/positions`)
        ]);

        const storyData = await storyRes.json();
        if (storyData.error) throw new Error(storyData.error);
        
        let positionsData = {};
        if (positionsRes.ok) {
          positionsData = await positionsRes.json();
        }

        processStoryData(storyData, positionsData);

      } catch (error) {
        console.error("Ошибка при загрузке данных:", error);
        alert("Не удалось загрузить историю или ее карту.");
      }
    };
    fetchData();
  }, [userId, storyId, processStoryData]);


  // --- Обработчики событий React Flow ---

  /**
   * Вызывается в начале создания новой связи.
   */
  const onConnectStart = useCallback((event, { nodeId }) => {
    connectingNodeId.current = nodeId;
    wasConnectedRef.current = false; // сбрасываем перед новым соединением
  }, []);

  /**
   * Создает новую связь между существующими узлами.
   */
  const onConnect = useCallback(async (connection) => {
    if (!connection.source || !connection.target) return;
    
    wasConnectedRef.current = true; // <-- Флаг, что была связь

    const choiceText = prompt('Введите текст для кнопки этого выбора:');
    if (!choiceText) return;
    
    try {
      await apiConnectFragments(userId, storyId, connection.source, connection.target, choiceText);

      let newChoiceIndex;

      setNodes((nds) =>
        nds.map((node) => {
          if (node.id === connection.source) {
            const newChoice = { target: connection.target, text: choiceText, effects: [] };
            newChoiceIndex = node.data.fragment.choices ? node.data.fragment.choices.length : 0;

            const updatedChoices = node.data.fragment.choices
              ? [...node.data.fragment.choices, newChoice]
              : [newChoice];

            return {
              ...node,
              data: {
                ...node.data,
                fragment: {
                  ...node.data.fragment,
                  choices: updatedChoices,
                },
              },
            };
          }
          return node;
        })
      );

      const newEdge = {
        id: `${connection.source}->${connection.target}_${newChoiceIndex}`,
        source: connection.source,
        target: connection.target,
        type: 'custom',
        animated: true,
        style: { stroke: '#2563eb' },
        data: {
          text: choiceText,
          effectsText: '',
          originalEffects: [],
          choiceIndex: newChoiceIndex,
        },
      };

      setEdges((eds) => eds.concat(newEdge));
    } catch (e) {
      alert(`Ошибка создания связи: ${e.message}`);
    }
  }, [userId, storyId, setEdges, setNodes]);

  /**
   * Создает новый узел и связь с ним при "бросании" линии на пустое место.
   */

  const onConnectEnd = useCallback(async (event) => {
    const wasConnected = wasConnectedRef.current;
    wasConnectedRef.current = false;

    if (wasConnected) return;

    const sourceNodeId = connectingNodeId.current;
    connectingNodeId.current = null;

    if (!sourceNodeId) return;

    let clientX = event.clientX;
    let clientY = event.clientY;

    // 💡 Обработка touch-событий
    if (event.type === 'touchend' && event.changedTouches && event.changedTouches.length > 0) {
      clientX = event.changedTouches[0].clientX;
      clientY = event.changedTouches[0].clientY;
    }

    if (typeof clientX !== 'number' || typeof clientY !== 'number') return;

    const position = screenToFlowPosition({ x: clientX, y: clientY });

    let rawInput = prompt("Введите имя нового фрагмента (например ИдтиВперёд):");
    if (!rawInput) return;

    const currentNodeIds = new Set(getNodes().map((n) => n.id));
    const newName = generateUniqueFragmentId(rawInput.trim(), currentNodeIds);
    const choiceText = prompt(`Введите текст кнопки для перехода к "${newName}":`);
    if (!choiceText) return;

    try {
      const { story } = await apiCreateAndConnect(userId, storyId, sourceNodeId, newName, choiceText);
      const sourceFragment = story.fragments[sourceNodeId];
      const newChoiceIndex = sourceFragment.choices.findIndex(c => c.target === newName);

      if (newChoiceIndex === -1) {
        throw new Error("Не удалось найти созданный выбор в ответе от сервера.");
      }

      const newNode = {
        id: newName,
        type: 'custom',
        position,
        data: {
          label: `${newName}\n(пусто)`,
          bgColor: '#fecaca',
          media: [],
          fragment: story.fragments[newName],
        },
      };

      const newEdge = {
        id: `${sourceNodeId}->${newName}_${newChoiceIndex}`,
        source: sourceNodeId,
        target: newName,
        type: 'custom',
        animated: true,
        style: { stroke: '#2563eb' },
        data: {
          text: choiceText,
          effectsText: '',
          originalEffects: [],
          choiceIndex: newChoiceIndex,
        },
      };

      setNodes((nds) => nds.concat(newNode));
      setEdges((eds) => eds.concat(newEdge));

      setTimeout(saveCurrentNodePositions, 100);
    } catch (e) {
      alert(`Ошибка создания фрагмента: ${e.message}`);
    }
  }, [userId, storyId, screenToFlowPosition, setNodes, setEdges, saveCurrentNodePositions]);
  // --- НОВЫЙ ОБРАБОТЧИК: Сохранение после перетаскивания ---
  const onNodeDragStop = useCallback(() => {
      saveCurrentNodePositions();
  }, [saveCurrentNodePositions]);
  /**
   * Оптимизированный обработчик для кликов (левый и правый) по узлу. Открывает контекстное меню.
   */
  const handleNodeInteraction = useCallback((event, node) => {
    event.preventDefault();
    const pane = reactFlowWrapper.current.getBoundingClientRect();
    setMenu({
      id: node.id,
      top: event.clientY,
      left: event.clientX,
      // Логика для предотвращения выхода меню за пределы экрана (опционально)
      // right: event.clientX >= pane.width - 200 ? pane.width - event.clientX : undefined,
      // bottom: event.clientY >= pane.height - 200 ? pane.height - event.clientY : undefined,
      node: node, // Передаем данные узла в меню
    });
  }, [setMenu]);

  /**
   * Оптимизированный обработчик для кликов (левый и правый) по ребру. Открывает меню ребра.
   */
  const handleEdgeInteraction = useCallback((event, edge) => {
    event.preventDefault();
    event.stopPropagation(); // Предотвращаем срабатывание onPaneClick
    setEdgeMenu({
      id: edge.id,
      top: event.clientY,
      left: event.clientX,
      edge: edge, // Передаем данные ребра в меню
    });
  }, [setEdgeMenu]);

  /**
   * Закрывает все открытые контекстные меню при клике на пустую область.
   */
  const onPaneClick = useCallback(() => {
    setMenu(null);
    setEdgeMenu(null);
  }, [setMenu, setEdgeMenu]);

  /**
   * Обработчик сохранения эффектов из модального окна.
   */
  const handleSaveEffects = useCallback(async (newEffects) => {
    const { edge } = effectsModal;
    if (!edge) return;

    try {
      await apiUpdateChoice(userId, storyId, edge.source, edge.data.choiceIndex, {
        effects: newEffects
      });

      // Локально обновляем данные ребра для мгновенного отображения
      setEdges((eds) =>
        eds.map((e) => {
          if (e.id === edge.id) {
            const newEffectText = newEffects.length ? newEffects.map(ef => `${ef.stat}: ${ef.value}`).join(', ') : '';
            return {
              ...e,
              data: {
                ...e.data,
                originalEffects: newEffects,
                effectsText: newEffectText,
              },
            };
          }
          return e;
        })
      );

      setEffectsModal({ isOpen: false, edge: null }); // Закрываем модалку
    } catch (e) {
      alert(`Ошибка сохранения эффектов: ${e.message}`);
    }
  }, [effectsModal, userId, storyId, setEdges]);


  // --- Кастомные типы узлов и ребер ---
  const nodeTypes = useMemo(() => ({ custom: CustomNode }), []);
  const edgeTypes = useMemo(() => ({ custom: CustomEdge }), []);


  // --- Отображение компонента (Render) ---
  return (
    <div style={{ height: '100vh', width: '100%' }} ref={reactFlowWrapper}>
      <ReactFlow
        nodes={nodes}
        edges={edges}
        onNodesChange={onNodesChange}
        onEdgesChange={onEdgesChange}
        nodeTypes={nodeTypes}
        edgeTypes={edgeTypes}
        onConnect={onConnect}
        onConnectStart={onConnectStart}
        onConnectEnd={onConnectEnd} // Этот обработчик теперь работает и для мыши, и для тач-событий
        onPaneClick={onPaneClick}
        onNodeDragStop={onNodeDragStop} // <-- ДОБАВЛЕН НОВЫЙ ПРОПС        
        onNodeClick={handleNodeInteraction} // Используем единый обработчик
        onNodeContextMenu={handleNodeInteraction} // И здесь тоже
        onEdgeClick={handleEdgeInteraction} // Единый обработчик для ребер
        onEdgeContextMenu={handleEdgeInteraction} // И здесь
        fitView
        minZoom={0.1}
        maxZoom={2}
      >
        <Controls />
        <Background />
        
        {showMiniMap && (
          <MiniMap nodeColor={(node) => node.data.bgColor} pannable zoomable />
        )}
        
        {menu && (
          <ContextMenu
            onClick={onPaneClick}
            setNodes={setNodes}
            setEdges={setEdges}
            {...menu}
          />
        )}
        
        {edgeMenu && (
          <EdgeContextMenu
            {...edgeMenu}
            userId={userId}
            storyId={storyId}
            onClick={() => setEdgeMenu(null)}
            onEditEffects={() => {
              setEffectsModal({ isOpen: true, edge: edgeMenu.edge });
              setEdgeMenu(null);
            }}
            setEdges={setEdges}
          />
        )}
      </ReactFlow>

      {/* Кнопка для переключения видимости миникарты */}
      <button
        onClick={() => setShowMiniMap(!showMiniMap)}
        style={{
          position: 'absolute',
          bottom: 10,     // Перенос вниз
          left: 60,       // Немного правее левого края
          zIndex: 10,
          background: '#fff',
          border: '1px solid #ccc',
          borderRadius: '6px',
          padding: '4px 8px',
          cursor: 'pointer',
          fontSize: '12px'
        }}
      >
        {showMiniMap ? 'Миникарта' : 'Миникарта'}
      </button>

      {/* Модальное окно для редактирования эффектов */}
      <EffectsModal
        isOpen={effectsModal.isOpen}
        edge={effectsModal.edge}
        onClose={() => setEffectsModal({ isOpen: false, edge: null })}
        onSave={handleSaveEffects}
      />
    </div>
  );
}

// --- Компонент-обертка с провайдером React Flow ---
// Это необходимо, чтобы хук useReactFlow работал в компоненте App.
function Flow() {
  return (
    <ReactFlowProvider>
      <App />
    </ReactFlowProvider>
  );
}

// Экспортируем вспомогательные функции, если они нужны в других частях приложения
export { getNodeColor, mediaLabel };
export default Flow;