// api.js

export async function apiUpdateFragmentText(userId, storyId, fragmentId, newText) {
  await fetch(`/api/story/${userId}/${storyId}/fragment/${fragmentId}/text`, {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({ text: newText }),
  });
}

export async function apiDeleteFragment(userId, storyId, fragmentId) {
  const response = await fetch(`/api/story/${userId}/${storyId}/fragment/${fragmentId}`, {
    method: 'DELETE',
  });
  return response.ok;
}

export async function apiRenameFragment(userId, storyId, oldName, newName) {
  const response = await fetch(`/api/story/${userId}/${storyId}/fragment/${oldName}/rename`, {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({ newName }),
  });
  if (!response.ok) {
    const { error } = await response.json();
    throw new Error(error);
  }
  return await response.json();
}

export async function apiConnectFragments(userId, storyId, source, target, text) {
  const response = await fetch(`/api/story/${userId}/${storyId}/connect`, {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({ source, target, text }),
  });
  return await response.json();
}

export async function apiCreateAndConnect(userId, storyId, source, newName, choiceText) {
  const response = await fetch(`/api/story/${userId}/${storyId}/create_and_connect`, {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({ source, newName, choiceText }),
  });
  if (!response.ok) {
    const { error } = await response.json();
    throw new Error(error);
  }
  return await response.json();
}

export async function apiUpdateChoice(userId, storyId, sourceId, choiceIndex, updates) {
  const response = await fetch(`/api/story/${userId}/${storyId}/choice`, {
    method: 'PUT',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({
      source: sourceId,
      choiceIndex: choiceIndex,
      ...updates // Передаем объект с обновлениями { text, effects }
    }),
  });
  if (!response.ok) {
    const { error } = await response.json();
    throw new Error(error);
  }
  return await response.json();
}