import React, { useState, useEffect } from 'react';

const modalOverlayStyle = {
  position: 'fixed',
  top: 0,
  left: 0,
  right: 0,
  bottom: 0,
  backgroundColor: 'rgba(0, 0, 0, 0.6)',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  zIndex: 1000,
};

const modalContentStyle = {
  background: 'white',
  padding: '20px',
  borderRadius: '8px',
  width: '90%',
  maxWidth: '500px',
  maxHeight: '80vh',
  overflowY: 'auto',
  display: 'flex',
  flexDirection: 'column',
  gap: '15px',
  position: 'relative',
};

const effectRowStyle = {
  display: 'grid',
  gridTemplateColumns: '2fr 2fr auto auto',
  gap: '10px',
  alignItems: 'center',
  border: '1px solid #ccc',
  padding: '10px',
  borderRadius: '6px',
  ...(window.innerWidth < 500 ? { gridTemplateColumns: '1fr' } : {}),
};

const inputStyle = {
  padding: '8px',
  borderRadius: '4px',
  border: '1px solid #ddd',
  width: '100%',
  boxSizing: 'border-box',
};

const buttonStyle = {
  padding: '8px 12px',
  borderRadius: '4px',
  border: 'none',
  cursor: 'pointer',
};

function EffectsModal({ isOpen, onClose, onSave, edge }) {
  const [effects, setEffects] = useState([]);
  const [isHelpOpen, setIsHelpOpen] = useState(false); // ✅ перемещено наверх

  useEffect(() => {
    if (isOpen && edge?.data?.originalEffects) {
      setEffects(JSON.parse(JSON.stringify(edge.data.originalEffects)));
    } else {
      setEffects([]);
    }
  }, [isOpen, edge]);

  if (!isOpen) return null;

  const handleEffectChange = (index, field, value) => {
    const newEffects = [...effects];
    newEffects[index][field] = field === 'hide' ? value.target.checked : value.target.value;
    setEffects(newEffects);
  };

  const handleAddEffect = () => {
    setEffects([...effects, { stat: '', value: '', hide: false }]);
  };

  const handleDeleteEffect = (index) => {
    setEffects(effects.filter((_, i) => i !== index));
  };

  const handleSave = () => {
    const isValid = effects.every(e => e.stat.trim() !== '' && e.value.trim() !== '');
    if (!isValid) {
      alert('Пожалуйста, заполните поля "Стат" и "Значение" для всех эффектов.');
      return;
    }
    onSave(effects);
  };

  return (
    <div style={modalOverlayStyle} onClick={onClose}>
      <div style={modalContentStyle} onClick={(e) => e.stopPropagation()}>
        <h3 style={{ marginTop: 0, borderBottom: '1px solid #eee', paddingBottom: '10px' }}>
          Редактирование эффектов для связи: {edge.source} → {edge.target}
        </h3>

        {effects.map((effect, index) => (
          <div key={index} style={effectRowStyle}>
            <input
              type="text"
              placeholder="Атрибут (например, сила)"
              value={effect.stat}
              onChange={(e) => handleEffectChange(index, 'stat', e)}
              style={inputStyle}
            />
            <input
              type="text"
              placeholder="Значение (например, +2)"
              value={effect.value}
              onChange={(e) => handleEffectChange(index, 'value', e)}
              style={inputStyle}
            />
            <label style={{ display: 'flex', alignItems: 'center', gap: '5px' }}>
              <input
                type="checkbox"
                checked={effect.hide}
                onChange={(e) => handleEffectChange(index, 'hide', e)}
              />
              Скрыть
            </label>
            <button
              style={{ ...buttonStyle, background: '#f8d7da', color: '#721c24' }}
              onClick={() => handleDeleteEffect(index)}
            >
              🗑️
            </button>
          </div>
        ))}

        <button
          style={{ ...buttonStyle, background: '#cce5ff', color: '#004085' }}
          onClick={handleAddEffect}
        >
          + Добавить эффект
        </button>

        <div style={{ display: 'flex', justifyContent: 'flex-end', gap: '10px', marginTop: '20px' }}>
          <button style={{ ...buttonStyle, background: '#e2e3e5' }} onClick={onClose}>
            Отмена
          </button>
          <button style={{ ...buttonStyle, background: '#d4edda', color: '#155724' }} onClick={handleSave}>
            Сохранить изменения
          </button>
        </div>

        {/* Кнопка помощи */}
        <button
          onClick={() => setIsHelpOpen(true)}
          style={{
            position: 'absolute',
            bottom: '20px',
            left: '20px',
            borderRadius: '50%',
            width: '32px',
            height: '32px',
            fontSize: '18px',
            fontWeight: 'bold',
            background: '#e2e3e5',
            border: '1px solid #ccc',
            cursor: 'pointer',
          }}
        >
          ?
        </button>

        {/* Подсказка */}
        {isHelpOpen && (
          <div
            style={{
              position: 'fixed',
              bottom: '80px',
              left: '20px',
              background: '#fff',
              border: '1px solid #ccc',
              borderRadius: '6px',
              padding: '12px',
              width: '320px',
              boxShadow: '0 2px 8px rgba(0,0,0,0.2)',
              zIndex: 1100,
              fontSize: '14px',
              lineHeight: '1.5',
              maxHeight: '300px',      // 👈 ограничение высоты
              overflowY: 'auto',        // 👈 прокрутка при переполнении
            }}
          >
            <div style={{ marginBottom: '8px' }}>
              <strong>Подсказка:</strong><br />
              Эффекты позволяют изменить параметры персонажа.
              <br />
              Пример: <code>стат: сила</code>, <code>значение: +2</code><br />
              Чекбокс <strong>"Скрыть"</strong> означает, что эффект будет применён, но не показан игроку.
            </div>

            <div style={{ marginBottom: '8px' }}>
              <strong>Атрибуты:</strong><br />
              Атрибут может быть любым — например, характеристиками (<code>сила</code>, <code>ловкость</code>) или событиями
              (<code>письмо</code>, <code>встреча</code>), которые можно использовать далее (см. обучение в главном меню бота).
            </div>

            <div style={{ marginBottom: '8px' }}>
              <strong>Варианты значений:</strong>
              <ol style={{ paddingLeft: '18px', margin: '4px 0' }}>
                <li>
                  <strong>Просто число</strong> (<code>2</code>, <code>8</code>, <code>47</code>) — задаёт атрибут. Если он уже существует, его значение будет перезаписано.
                </li>
                <li>
                  <strong>Число со знаком</strong> (<code>+4</code>, <code>-16</code>) — изменяет текущее значение атрибута. Если атрибут отсутствует, используется <code>0</code> как начальное значение.
                </li>
                <li>
                  <strong>Проверка</strong> (<code>=4</code>, <code>&gt;8</code>, <code>&lt;2</code>) — проверяет соответствие значению. При несоответствии действие будет недоступно, а если стоит галочка "Скрыть" — кнопка даже не отобразится.
                </li>
              </ol>
              Атрибуты обрабатываются строго <strong>в порядке добавления</strong> (см. обучение).
            </div>

            <div style={{ marginBottom: '4px' }}>
              <strong>Кнопка "Скрыть"</strong> убирает уведомления об изменении атрибутов и скрывает кнопки, если проверка не пройдена.
            </div>

            <button
              onClick={() => setIsHelpOpen(false)}
              style={{
                ...buttonStyle,
                background: '#f5c6cb',
                color: '#721c24',
                float: 'right',
                marginTop: '10px',
              }}
            >
              Закрыть
            </button>
          </div>
        )}
      </div>
    </div>
  );
}

export default EffectsModal;
